body{
  background-color: skyblue;
}

.App{
  background-color: royalblue;
  width: 400px;
  min-height: 500px;
  margin: 40px auto;
}

#to-do-form input{
  background-color: turquoise;
  width: 200px;
  height: 50px;
  padding: 0 20px;
  color:white;
  margin: 20px;
  border: 0;
  border-radius: 10px;
  font-size: 18px;
}

#to-do-form button{
  background-color: orange;
  font-weight: bolder;
  height: 50px;
  width: 80px;
  border: 0;
  border-radius: 5px;
  padding: 0 20px;
  cursor: pointer;
  font-size: 18px;
  color: white;
}

#to-do-form input::placeholder{
  color:white;
}

#to-do-form input:focus,#to-do-form button:focus{
  outline: none;
}